.dna-container {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  background: transparent;
}

/* ===== Ambient glow ===== */
.dna-container::before {
  content: "";
  position: absolute;
  width: 500px;
  height: 700px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: radial-gradient(
    ellipse at center,
    rgba(0, 180, 216, 0.08) 0%,
    rgba(67, 97, 238, 0.04) 25%,
    rgba(123, 47, 247, 0.02) 50%,
    transparent 70%
  );
  filter: blur(50px);
  pointer-events: none;
  animation: ambient-pulse 4s ease-in-out infinite alternate;
}

/* ===== The helix column ===== */
.dna-helix {
  position: absolute;
  top: -8%;
  bottom: -8%;
  left: 50%;
  transform: translateX(-50%);
  width: 200px;
}

/* ===== Each rung row ===== */
.dna-rung {
  position: absolute;
  left: 0;
  width: 100%;
  height: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* ===== Connector bar ===== */
.dna-rung-bar {
  position: absolute;
  left: 50%;
  top: 50%;
  height: 2px;
  border-radius: 1px;
  transform-origin: left center;
  /* width and transform set via inline style */
}

/* ===== 3D Sphere ===== */
.dna-sphere {
  position: absolute;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  top: 50%;
  margin-top: -9px;
  /* translateX and scale set via animation */
}

/* Specular highlight */
.dna-sphere::before {
  content: "";
  position: absolute;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  top: 3px;
  left: 5px;
  background: rgba(255, 255, 255, 0.7);
  filter: blur(1.5px);
}

/* Soft fill light */
.dna-sphere::after {
  content: "";
  position: absolute;
  width: 10px;
  height: 4px;
  border-radius: 50%;
  top: 2px;
  left: 3px;
  background: rgba(255, 255, 255, 0.12);
  filter: blur(2px);
}

/* ===== Cyan sphere (strand A) ===== */
.dna-sphere-a {
  background: radial-gradient(
    circle at 32% 28%,
    #a5f3fc,
    #22d3ee 25%,
    #0891b2 50%,
    #065f73 75%,
    #033d4a 100%
  );
  box-shadow:
    0 0 5px 2px rgba(0, 180, 216, 0.35),
    0 0 12px 4px rgba(0, 180, 216, 0.15),
    inset 0 -4px 7px rgba(0, 0, 0, 0.4),
    inset 2px 2px 4px rgba(255, 255, 255, 0.06);
  animation: strand-a 8s ease-in-out infinite;
}

/* ===== Purple sphere (strand B) ===== */
.dna-sphere-b {
  background: radial-gradient(
    circle at 32% 28%,
    #e9d5ff,
    #a78bfa 25%,
    #7c3aed 50%,
    #4c1d95 75%,
    #2e1065 100%
  );
  box-shadow:
    0 0 5px 2px rgba(139, 92, 246, 0.35),
    0 0 12px 4px rgba(139, 92, 246, 0.15),
    inset 0 -4px 7px rgba(0, 0, 0, 0.4),
    inset 2px 2px 4px rgba(255, 255, 255, 0.06);
  animation: strand-b 8s ease-in-out infinite;
}

/* ===== Floating particles ===== */
.dna-particle {
  position: absolute;
  border-radius: 50%;
  pointer-events: none;
}

.dna-particle-drift {
  animation: dna-float linear infinite;
}

.dna-particle-twinkle {
  animation: dna-twinkle ease-in-out infinite;
}

/* ===== Light rays ===== */
.dna-ray {
  position: absolute;
  pointer-events: none;
  border-radius: 50%;
  filter: blur(30px);
  animation: dna-ray-pulse ease-in-out infinite alternate;
}

/* ===========================================================
   KEYFRAMES
   =========================================================== */

/* Strand A: moves right when "close" (scale up), left when "far" (scale down) */
@keyframes strand-a {
  0%   { transform: translateX(-70px) scale(0.5);  opacity: 0.45; z-index: 0; }
  25%  { transform: translateX(0px)   scale(0.85); opacity: 0.7;  z-index: 1; }
  50%  { transform: translateX(70px)  scale(1.15); opacity: 1;    z-index: 2; }
  75%  { transform: translateX(0px)   scale(0.85); opacity: 0.7;  z-index: 1; }
  100% { transform: translateX(-70px) scale(0.5);  opacity: 0.45; z-index: 0; }
}

/* Strand B: opposite phase — close when A is far */
@keyframes strand-b {
  0%   { transform: translateX(70px)  scale(1.15); opacity: 1;    z-index: 2; }
  25%  { transform: translateX(0px)   scale(0.85); opacity: 0.7;  z-index: 1; }
  50%  { transform: translateX(-70px) scale(0.5);  opacity: 0.45; z-index: 0; }
  75%  { transform: translateX(0px)   scale(0.85); opacity: 0.7;  z-index: 1; }
  100% { transform: translateX(70px)  scale(1.15); opacity: 1;    z-index: 2; }
}

@keyframes ambient-pulse {
  0%   { opacity: 0.4; }
  100% { opacity: 0.7; }
}

@keyframes dna-float {
  0%   { opacity: 0; transform: translateY(0) scale(0.3); }
  15%  { opacity: 1; }
  85%  { opacity: 0.8; }
  100% { opacity: 0; transform: translateY(-250px) translateX(30px) scale(0); }
}

@keyframes dna-twinkle {
  0%, 100% { opacity: 0.2; transform: scale(0.8); }
  50%      { opacity: 1;   transform: scale(1.3); }
}

@keyframes dna-ray-pulse {
  0%   { opacity: 0.08; }
  100% { opacity: 0.18; }
}
